<template>
  <div class="container">

    <h1>Available on Amazon Prime</h1>

    <div>
      <h3>Get Prime IDs</h3>
      <label for="imdbStart">start</label>
      <input id="imdbStart" type="text" v-model="imdbStart" />
      <label for="imdbCount">count</label>
      <input id="imdbCount" type="text" v-model="imdbCount" />
      <button @click="scrapePrimeIds">Scrape</button>


    </div>


<br />

    <!-- <div class="filter" >
      <label for="filterYear">Release Year</label>
      <input id="filterYear" type="text" v-model="year" />
      <label for="filterCount">Count</label>
      <input id="filterCount" type="text" v-model="count" />
