      <span v-if="allMovies">Count: {{ resultCount }}</span>
      <button @click="getMovies">filter</button>
    </div> -->

    <table>
      <tr>
        <th>Prime ImdbIds {{resultCount}}</th>
      </tr>
      <tr>
          <p>{{primeIds}}</p>
      </tr>
      
    </table>
  </div>
</template>

<script>
import PrimeidService from './../PrimeidService'

export default {
  name: 'PrimeIds',
  data() {
    return {
      primeIds: [],
      resultPrimeIds: [],
      year: '',
      count: 20,
      page: 1,
      imdbStart: 1,
      imdbCount: 250,
    }
  },
  computed: {
    resultCount() {
      return this.primeIds.length
    }
  },
  methods: {
    async scrapePrimeIds() {
      this.resultPrimeIds = await PrimeidService.getPrimeIds({
        start: this.imdbStart,
        count: this.imdbCount,
      })
    let arrayLength = this.resultPrimeIds.length;
      for (var i = 0; i < arrayLength; i++) {
          let id = this.resultPrimeIds[i]
           if ( !this.primeIds.includes(id) ) {
               this.primeIds.push(id)
           }
      }
    },
  }
}
</script>

